import jsPDF from 'jspdf';
import 'jspdf-autotable';
//data, progress, course, instituition, initialDate, finalDate
export default function generatePdf(data, progress, course, instituition, initialDate, finalDate) {
  let prog;

  if (!progress) { prog = 'Em aberto' }

  //FORMATAÇÂO DA DATA dd/mm/aa
  let initialDateFormated, finalDateFormated;

  const firstDate = initialDate.split('-');
  const secondDate = finalDate.split('-');

  const dayFirstDate = firstDate[2];
  const monthFirstDate = firstDate[1];
  const yearFirstDate = firstDate[0];

  const daySecondDate = secondDate[2];
  const monthSecondDate = secondDate[1];
  const yearSecondDate = secondDate[0];

  const firstDateFormated = dayFirstDate + "/" + monthFirstDate + "/" + yearFirstDate;
  const secondDateFormated = daySecondDate + "/" + monthSecondDate + "/" + yearSecondDate;

  initialDateFormated = firstDateFormated;
  finalDateFormated = secondDateFormated;

  //COLUNAS DA TABELA
  var columns = [
    {title: "Aluno", dataKey: "name"},
    {title: "Telefone", dataKey: "phone"},
    {title: "Curso", dataKey: "course"},
    {title: "Indicador", dataKey: "application_user_name"},
    {title: "Instituição", dataKey: "instituition"},
  ];

  const doc = new jsPDF({
    orientation: 'p',
    unit: 'mm',
    format: 'a4',
    lineHeight: 1
  });
  
  var imgData = 'data:image/png;base64,'
  doc.addImage(imgData, 'PNG', 10, 10, 80, 30);
  
  doc.setFontSize(18);
  doc.setFont('helvetica');
  doc.text(200,15, "Faculdade Fasipe", {align:'right'});

  doc.setFont('helvetica');
  doc.setFontSize(13);

  doc.text(200,25, "Av. Magda Cassia Pissinati, Nº 69", {align:'right'});
  doc.text(200,32, "Residencial Florença - Sinop - MT", {align:'right'});
  doc.text(200,40, "(66) 3517-1320", {align:'right'});

  doc.setLineWidth(0.3);

  if (course && instituition) {
    doc.setFont('helvetica', 'bold');
    doc.setFontSize(15);
    doc.text(100,53, "Relatório de "+ course + " - " + instituition, {align:'center'});
  } else if (course) {
    doc.setFont('helvetica', 'bold');
    doc.setFontSize(15);
    doc.text(100,53, "Relatório de "+ course, {align:'center'});
  } else if (instituition) {
    doc.setFont('helvetica', 'bold');
    doc.setFontSize(15);
    doc.text(100,53, instituition, {align:'center'});
  }

  doc.line(10, 55, 200, 55);
  
  doc.setFont('helvetica');
  doc.setFontSize(15);
  doc.text(100,62, "Situação das indicações: " + (prog || progress), {align: 'center'});

  doc.setLineWidth(0.3);
  doc.line(10, 65, 200, 65);

  doc.setFont('helvetica', "bold");
  doc.setFontSize(12);
  doc.text(100,72, "Período: " + initialDateFormated + " - " + finalDateFormated, {align: 'center'});
  
  doc.setLineDash([0.5, 0.5], 0);
  doc.setLineWidth(0.3);
  doc.line(10, 75, 200, 75);

  doc.setLineDash([0, 0], 0);
  doc.setFontSize(11);

  doc.autoTable(columns, data, {startY: 80, theme: 'grid', styles: {halign: 'center', valign: 'middle', fontSize: 12}});

  var blob = doc.output("blob");
  window.open(URL.createObjectURL(blob));
}