import React, { FormEvent, useState, useEffect, useCallback } from "react";
import SidebarMini from '../../components/SidebarMini';
import { FaCog } from "react-icons/fa";
import api from "../../services/api";
import '../../styles/pages/users.css';
import { FiPlus } from "react-icons/fi";
import { Modal } from "@material-ui/core";
import Top10CourseBox from "../../components/Top10CourseBox";
import CourseContext from "../../components/contexts/CourseContext";

interface Course {
  id: number;
  name: string;
  alias?: string;
}

export default function Top10Courses() {
  const token = sessionStorage.getItem("@T_A");

  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(false);

  const [courses, setCourses] = useState<Course[]>([]);

  const [name, setName] = useState('');
  
  const getCourses = useCallback(() => {
    setLoading(true);

    api.get('/top10/courses', {
      headers: {
        "Authorization": "Bearer " + token,
        "Content-Type": "application/json"
      }
    }).then(response => {
      setCourses(response.data);
      setLoading(false);
    }).catch(err => {
      console.log("Error: ", err);
    });
  }, [token]);

  useEffect(() => {
    getCourses();
  }, [getCourses]);

  function createCourse(event: FormEvent) {
    event.preventDefault();
    
    const data = JSON.stringify({
      name: name
    });

    api.post('/top10/courses', data, {
      headers: {
        "Content-Type": "application/json",
        "Authorization": "Bearer " + token
      }
    }).then(response => {
      alert(response.data.message);

      if (response.status === 200) {
        getCourses();
        setOpen(false);
      }
    }).catch(err => {
      console.log(err);
    });
  }

  const modalBody = (
    <div className="modal-body">
      <fieldset>
        <legend>Cadastrar curso</legend>
        <form className="update-user-form" onSubmit={createCourse}>
          <div className="input-block">
            <label>Nome</label>
            <input defaultValue={name} onChange={(event) => setName(event.currentTarget.value)}></input>
          </div>
          <button
            className="confirm-button"
            type="submit"
            disabled={
              !name
            }>
            Cadastrar
          </button>
        </form>
      </fieldset>
    </div>
  );

  return (
    <div id="page-users">
      <SidebarMini top10={true} />

      <main>
        <div className="indications-boxes-content">
        <button className="add-button" type="button" onClick={() => setOpen(true)}>
          Adicionar
          <FiPlus size={32} color="white" />
        </button>
          {
            loading ? 
            (
              <div className="rotate-box">
                <FaCog size={30} color="#3CDC8C" className="rotating" />
              </div>
            ) : (
              <CourseContext.Provider value={{ getCourses }}>
                {
                  courses.map(course => (
                    <Top10CourseBox key={course.id} user={course} />
                  ))
                }
              </CourseContext.Provider>
            )
          }
        </div>
        
        <Modal
          open={open}
          onClose={() => setOpen(false)}
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description"
          style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center'
          }}
        >
          {modalBody}
        </Modal>
      </main>
    </div>
  );
}
