import React, { useState, FormEvent } from 'react';
import '../styles/pages/recover-password.css';
import api from '../services/api';

import { FaCheck, FaExclamationTriangle, FaSpinner } from 'react-icons/fa';

export default function RecoverPassword() {
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState(false);
  const [callback, setCallback] = useState(false);
  const [validation, setValidation] = useState(true);

  const [message, setMessage] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [messageValidation, setMessageValidation] = useState('');

  function recoverPassword(event: FormEvent) {
    event.preventDefault();

    if (password === confirmPassword) {
      setLoading(true);

      let hashed = window.location.href.toString().split("/recover-password/")[1];

      const body = JSON.stringify({
        password: password,
        hash: hashed
      });
  
      api.put("/users/application/recover-password", body, {
        headers: {
          "Content-Type": "application/json"
        }
      }).then(response => {
        setTimeout(() => {
          setLoading(false);
          setCallback(true);
          if (response.status === 200) {
            setSuccess(true);
          } else {
            setError(true);
          }
          setMessage(response.data.message);
        }, 4000)
      }).catch(err => {
        setLoading(false);
        setCallback(true);
        setMessage(err.message);
      });
    } else {
      setValidation(true);
      setMessageValidation("Senhas diferentes.");
    }
  }

  return (
    <div className="main-recover">
      {
        loading ? (
          <div className="rotate-box">
            <FaSpinner size={50} color="#3CDC8C" className="rotating" />
          </div>
        ) : (
          callback ?
          <div className="box-center" id="callback-box">
              { success ? 
                <FaCheck size={130} color="#37B307" style={{marginBottom: 20}} />
              : null
              }
              
              { error ? 
                <FaExclamationTriangle size={130} color="#FA685E" style={{marginBottom: 20}} />
              : null
              }
              { message
              ? <div style={{width: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                  <span style={{fontSize: 32, fontWeight: 'bold', lineHeight: 1.5, textAlign: 'center', color: "black"}}>
                    {message}
                  </span>
                </div>
              : null
              }
          </div>
          :
          <div className="box-center-recover" id="form-box">
            <img src={require("../images/logo.png")} style={{width: '300px', marginTop: 20}} alt="Logo" />
            <form className="recover-password" onSubmit={(event) => recoverPassword(event)}>
              <fieldset>
                <legend>Recuperar senha</legend>
                <div className="input-block">
                  <label htmlFor="password">Nova senha</label>
                  <input 
                    id="password" 
                    type="password"
                    onChange={event => setPassword(event.target.value)}
                  />
                </div>
                <div className="input-block">
                  <label htmlFor="confirm-password">Confirmar Senha</label>
                  <input 
                    id="confirm-password"
                    type="password"
                    onChange={event => setConfirmPassword(event.target.value)}
                  />
                </div>
              </fieldset>
              {
                validation ? (
                  <span className="message-error">{ messageValidation }</span>
                ) : (
                  null
                )
              }
              <button className="confirm-button" type="submit" disabled={!password || !confirmPassword}>
                Enviar
              </button>
            </form>
          </div>
        )
      }
    </div>
  );
}