import React from 'react';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import '../styles/components/xlsx_button.css';

export default function XLSX({ id, table, filename }) {
  const date = new Date();

  const filenameFormated = filename + date.getTime();

  return (
    <ReactHTMLTableToExcel
      id={id}
      table={table}
      filename={filenameFormated}
      buttonText="Exportar em Excel"
      className="export-button"
      sheet="Página 1"
    />
  );
}