import React, { FormEvent, useState, useEffect, useCallback } from 'react';
import Top10IndicationBox from '../../components/Top10IndicationBox';
import SidebarMini from '../../components/SidebarMini';
import { FaCog } from 'react-icons/fa';
import { FiPrinter, FiTarget } from 'react-icons/fi';
import generatePdf from '../../components/PDFGenerator';
import Pagination from '../../components/Pagination';
import api from '../../services/api';
import '../../styles/pages/reports.css';
import IndicationContext from '../../components/contexts/IndicationContext';
import XLSX from '../../components/Xlsx';

const emptyGif = require('../../images/empty_gif.gif');

interface Indication {
  id: number;
  application_user_id: number;
  name: string;
  phone: string;
  course: string;
  course_id: number;
  instituition: string;
  instituition_id: number;
  application_user_name: string;
  application_user_lastname: string;
  application_user_ra: string;
  application_user_phone: string;
  commercial_note?: string;
  created_at: string;
  created_at_formated: string;
  updated_at: string;
  last_movement: string;
  status: number;
  protocol: String
}

interface Course {
  id: number;
  name: string;
  alias?: string;
}

interface Instituition {
  id: number;
  name: string;
}

export default function Reports() {
  const [queryValue, setQueryValue] = useState(1)

  function trocarValor(qua: String){
    setQueryValue(Number(qua));
    setProtocol("");
    setStudantName("");
  }

  const [indications, setIndications] = useState<Indication[]>([]);

  const token = sessionStorage.getItem('@T_A');

  const [printButtonEnable, setPrintButtonEnable] = useState(true);
  const [progressFilter, setProgressFilter] = useState(1);
  const [instituitionFilter, setInstituitionFilter] = useState(0);
  const [courseFilter, setCourseFilter] = useState(0);

  const [courses, setCourses] = useState<Course[]>([]);
  const [instituitions, setInstituitions] = useState<Instituition[]>([]);
  const [filteredIndications, setFilteredIndications] = useState<Indication[]>(
    []
  );

  // REPORT INFORMATION
  const [instituitionFilterLabel, setInstituitionFilterLabel] = useState('');
  const [courseFilterLabel, setCourseFilterLabel] = useState('');
  const [progressFilterLabel, setProgressFilterLabel] = useState('');
  const [initialDate, setInitialDate] = useState('');
  const [finalDate, setFinalData] = useState('');
  const [Name, setStudantName] = useState('');
  const [protocol, setProtocol] = useState('');

  // PAGINATION
  const [loading, setLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [postsPerPage] = useState(15);
  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;
  const currentIndications = filteredIndications.slice(
    indexOfFirstPost,
    indexOfLastPost
  );

  const getCourses = useCallback(() => {
    setLoading(true);

    api
      .get('/top10/courses', {
        headers: {
          Authorization: 'Bearer ' + token,
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        setCourses(response.data);
        setLoading(false);
      })
      .catch(err => {
        console.log('Error: ', err);
      });
  }, [token]);

  const getIndications = useCallback(() => {
    setLoading(true);

    api
      .get('/top10/indications', {
        headers: {
          Authorization: 'Bearer ' + token,
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        setIndications(response.data);
        setFilteredIndications(response.data);
        setLoading(false);
      })
      .catch(err => {
        console.log('Error: ', err);
      });
  }, [token]);

  const getInstituitions = useCallback(() => {
    setLoading(true);

    api
      .get('/top10/instituitions', {
        headers: {
          Authorization: 'Bearer ' + token,
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        setInstituitions(response.data);
        setLoading(false);
      })
      .catch(err => {
        console.log('Error: ', err);
      });
  }, [token]);

  useEffect(() => {
    getIndications();
    handlerDates();
    getInstituitions();
    getCourses();
  }, [getCourses, getIndications, getInstituitions]);

  function filterByIndicationName(name: string, ) {
    const value = name.toLowerCase();
    let criterio = String(queryValue)
    let filteredNamesArr = []
    if(criterio === "2"){
       filteredNamesArr = indications.filter(indication =>
        indication.application_user_ra == value
      );
    }else if(criterio === "1"){
       filteredNamesArr = indications.filter(indication =>
        indication.name.toLowerCase().includes(value)
      );
    }else if(criterio === "3"){
      filteredNamesArr = indications.filter(indication =>
       indication.protocol === value
      //  indication.protocol.includes(value)
     );
   }

    setFilteredIndications(filteredNamesArr);
    setCurrentPage(1);
  }

  function filterIndications(event: FormEvent) {
    event.preventDefault();

    setLoading(true);

    api
      .get(
        `/top10/indications/reports/?course=${courseFilter}&instituition=${instituitionFilter}&status=${progressFilter}&initialData=${initialDate}&finalData=${finalDate}&studantName=${Name}&protocol=${protocol}`,
        {
          headers: {
            Authorization: 'Bearer ' + token,
            'Content-Type': 'application/json'
          }
        }
      )
      .then(response => {
        setIndications(response.data);
        setFilteredIndications(response.data);
        setLoading(false);
        setCurrentPage(1);
        setPrintButtonEnable(true);
      })
      .catch(err => {
        console.log(err);
      });
  }

  function paginate(pageNumber: number) {
    setCurrentPage(pageNumber);
  }

  function instituitionFilterSettings(event: any) {
    const value = parseInt(event.currentTarget.value);

    let index = event.target.selectedIndex;
    let valueLabel = event.target[index].label;

    setInstituitionFilter(value);
    setInstituitionFilterLabel(valueLabel);
    setPrintButtonEnable(false);
  }

  function courseFilterSettings(event: any) {
    let valueLabel;

    let index = event.target.selectedIndex;
    const value = parseInt(event.currentTarget.value);

    if (value === 0) {
      valueLabel = null;
    } else {
      valueLabel = event.target[index].label;
    }

    setCourseFilter(value);
    setCourseFilterLabel(valueLabel);
    setPrintButtonEnable(false);
  }

  function progressFilterSettings(event: any) {
    let valueLabel;

    let index = event.target.selectedIndex;
    const value = parseInt(event.currentTarget.value);

    if (value === 0) {
      valueLabel = null;
    } else {
      valueLabel = event.target[index].label;
    }

    setProgressFilter(value);
    setProgressFilterLabel(valueLabel);
    setPrintButtonEnable(false);
  }

  function starterDateSettings(event: any) {
    setInitialDate(event);
    setPrintButtonEnable(false);
  }

  function finalDataSettings(event: any) {
    setFinalData(event);
    setPrintButtonEnable(false);
  }

  function handlerDates() {
    var date = new Date();

    var day: any = date.getDate();
    var month: any = date.getMonth() + 1;
    var year: any = date.getFullYear();

    var fifty: any = 0;
    if (day <= 15) {
      fifty = 1;
    } else {
      fifty = day - 15;
    }

    if (month < 10) {
      month = '0' + month;
    }
    if (day < 10) {
      day = '0' + day;
    }
    if (fifty < 10) {
      fifty = '0' + fifty;
    }

    var today = year + '-' + month + '-' + day;
    var fiftyDays = year + '-' + month + '-' + fifty;

    setInitialDate(fiftyDays);
    setFinalData(today);
  }












  return (
    <div id="page-indications">
      <SidebarMini top10={true} />

      <main>
        <div className="reports-boxes-content">
          <form className="filter-form" onSubmit={filterIndications}>
            <div className="input-block">
              <label>Progresso</label>
              <select
                onChange={event => progressFilterSettings(event)}
                defaultValue={progressFilter}
                className="select-status"
                placeholder="Progresso"
              >
                <option value="1">Em aberto</option>
                <option value="2">Indicação Válida</option>
                <option value="3">Inapta</option>
              </select>
            </div>
            <div className="input-block">
              <label>Curso</label>
              <select
                onChange={event => courseFilterSettings(event)}
                className="select-status"
                placeholder="Curso"
              >
                <option value="0" selected>
                  Não selecionado
                </option>
                {courses.map(course => (
                  <option key={course.id} value={course.id}>
                    {course.name}
                  </option>
                ))}
              </select>
            </div>
            <div className="input-block">
              <label>Unidade</label>
              <select
                onChange={event => instituitionFilterSettings(event)}
                className="select-status"
                placeholder="Progresso"
              >
                <option value="0" selected>
                  Não selecionado
                </option>
                {instituitions.map(instituition => (
                  <option key={instituition.id} value={instituition.id}>
                    {instituition.name}
                  </option>
                ))}
              </select>
            </div>
            <div className="dates">
              <div className="input-block">
                <label>A partir de:</label>
                <input
                  type="date"
                  className="date-input"
                  onChange={event =>
                    starterDateSettings(event.currentTarget.value)
                  }
                  defaultValue={initialDate}
                />
              </div>
              <div className="input-block">
                <label>Até:</label>
                <input
                  type="date"
                  className="date-input"
                  onChange={event =>
                    finalDataSettings(event.currentTarget.value)
                  }
                  defaultValue={finalDate}
                />
              </div>
              <div className="input-block"></div>
            </div>
            {/* <button
              className="print-button"
              type="button"
              onClick={() =>
                generatePdf(
                  filteredIndications,
                  progressFilterLabel,
                  courseFilterLabel,
                  instituitionFilterLabel,
                  initialDate,
                  finalDate
                )
              }
              disabled={filteredIndications.length === 0 || !printButtonEnable}
            >
              Imprimir
              <FiPrinter size={30} style={{ marginLeft: 10 }} />
            </button> */}

            <div className="input-block" >
              <select id='selectSelect' onChange={event => trocarValor(event.target.value)}>
                <option value="1">Pesquisar por nome do Indicado</option>
                <option value="2">Pesquisar por RA do Indicador</option>
                <option value="3">Pesquisar Protocolo</option>
              </select>
            </div>
            
            <button className="confirm-button" type="submit">
              Filtrar
            </button>
            <XLSX id="export-xlsx" table="table" filename="indicacoes_" />

            <div className="input-block">
              <input
                type="text"
                placeholder="Pesquisar"
                style={{
                  marginTop: 16
                }}
                onChange={event => {filterByIndicationName(event.target.value);
                  
                    if(queryValue === 1){
                      setStudantName(event.target.value);
                    }else if(queryValue === 3){
                      setProtocol(event.target.value);
                    };
                    console.log(queryValue);
         
                  }}
              />
            </div>



          </form>
          {loading ? (
            <div className="rotate-box">
              <FaCog size={30} color="#3CDC8C" className="rotating" />
            </div>
          ) : currentIndications.length > 0 ? (
            <>
              <IndicationContext.Provider value={{ getIndications }}>
                {currentIndications.map(indication => (
                  <Top10IndicationBox
                    key={indication.id}
                    indication={indication}
                  />
                ))}
              </IndicationContext.Provider>

              <table id="table" style={{ display: 'none' }}>
                <thead>
                  <tr>
                    <th>Aluno</th>
                    <th>Telefone</th>
                    <th>Curso</th>
                    <th>Indicador</th>
                    <th>Instituição</th>
                    <th>Data da indicação</th>
                    <th>Última movimentação</th>
                    <th>Notas do comercial</th>
                    <th>Status</th>
                  </tr>
                </thead>
                <tbody>
                  { queryValue == 2 ?
                  filteredIndications.map(indication => {
                      if(indication.status === 2){
                        return(
                        <tr key={indication.id}>
                          <td>{indication.name}</td>
                          <td>{indication.phone}</td>
                          <td>{indication.course}</td>
                          <td>
                            {indication.application_user_name}{' '}
                            {indication.application_user_lastname}
                          </td>
                          <td>{indication.instituition}</td>
                          <td>{indication.created_at_formated}</td>
                          <td>{indication.last_movement}</td>
                          <td>{indication.commercial_note}</td>
                          <td>{indication.status}</td>
                        </tr>)
                  }
                  }) : filteredIndications.map(indication => {
                      return(
                      <tr key={indication.id}>
                        <td>{indication.name}</td>
                        <td>{indication.phone}</td>
                        <td>{indication.course}</td>
                        <td>
                          {indication.application_user_name}{' '}
                          {indication.application_user_lastname}
                        </td>
                        <td>{indication.instituition}</td>
                        <td>{indication.created_at_formated}</td>
                        <td>{indication.last_movement}</td>
                        <td>{indication.commercial_note}</td>
                        <td>{indication.status}</td>
                      </tr>)
                })
                  }
                </tbody>
              </table>
            </>
          ) : (
            <div className="no-data-founded">
              <img
                src={emptyGif}
                width="200"
                alt="Nenhuma indicação encontrada"
              />
              <span>Parece que não existem indicações a serem filtradas.</span>
            </div>
          )}
          {loading ? null : (
            <div className="pagination">
              <Pagination
                postsPerPage={postsPerPage}
                totalPosts={filteredIndications.length}
                paginate={paginate}
              />
            </div>
          )}
        </div>
      </main>
    </div>
  );
}
