import React, { useState, useEffect } from "react";
import { PieChart } from "@opd/g2plot-react";
import api from '../services/api';

interface Data {
  type: string;
  value: number;
}

export default () => {
  const [data, setData] = useState<Data[]>([]);
  const [config] = useState({
    width: 300,
    height: 300,
    forceFit: true, 
    radius: 0.8,
    data: [],
    angleField: "value",
    colorField: "type",
    label: {
      visible: false,
      type: "inner"
    },
  });
  useEffect(() => {

    setTimeout(() => {
      setData([]);
    }, 1000);
    setTimeout(() => {
      getGraphicData();
    }, 2000);
  }, []);

  function getGraphicData() {
    api.get('/graphics/pizza').then(response => {
      setData(response.data);
    }).catch(err => {
      console.log(err);
    });
  }

  return (
    <section>
      <PieChart {...config} data={data} />
    </section>
  );
};
