import React, { FormEvent, useEffect, useState, useContext, memo } from 'react';
import IndicationContext from './contexts/IndicationContext';
import { FiPlus } from "react-icons/fi";
import { Modal } from '@material-ui/core';
import '../styles/components/indication_box.css';
import api from '../services/api';

interface Indication {
  application_user_id: number;
  name: string;
  phone: string;
  course: string;
  instituition: string;
  application_user_name: string;
  application_user_phone: string;
  commercial_note?: string;
  status: number;
  created_at_formated: string;
}

function IndicationBox({ indication }: any) {
  const { getIndications } = useContext(IndicationContext);
  
  const token = sessionStorage.getItem("@T_A");
  const Indication: Indication = indication;
  const [status, setStatus] = useState('');
  
  const [statusInput, setIndicationStatusInput] = useState(0);
  const [comercialNote, setComercialNote] = useState('');
  
  const [open, setOpen] = useState(false);

  useEffect(() => {
    switch (Indication.status) {
      case 1:
        setStatus('Em aberto');
        break;
      case 2:
        setStatus('Em andamento');
        break;
      case 3:
        setStatus('Matriculada');
        break;
      case 4:
        setStatus('Cancelada');
        break;
      default:
        setStatus('Indefinido');
    }

    setIndicationStatusInput(Indication.status);
  }, [Indication.status]);

  function updateIndication(event: FormEvent) {
    event.preventDefault();

    const data = {
      status: statusInput,
      commercial_note: comercialNote
    }

    api.put(`/indications/${indication.id}/commercial`, data, {
      headers: {
        "Authorization": "Bearer " + token
      }
    }).then(response => {
      alert(response.data.message);

      if (response.status === 200) {
        getIndications();
        setOpen(false);
      }
    }).catch(err => {
      console.log(err);
    })
  }

  const modalBody = (
    <div className="modal-body">
      <h3>{status}</h3>
      <fieldset>
        <legend>Dados do Indicador</legend>
        <p>Nome: {Indication.application_user_name}</p>
      </fieldset>
      <fieldset>
        <legend>Indicação</legend>
        <p>Indicado: {Indication.name}</p>
        <p>Telefone: {Indication.phone}</p>
        <p>Curso: {Indication.course}</p>
        <p>Unidade: {Indication.instituition}</p>
        <p>Data da indicação: {Indication.created_at_formated}</p>
      </fieldset>
      <fieldset>
        <legend>Atualização</legend>
        <form className="update-indication-form" onSubmit={updateIndication}>
          <div className="input-block">
            <label>Situação da indicação</label>
            <select
              defaultValue={statusInput}
              onChange={(event) => setIndicationStatusInput(parseInt(event.currentTarget.value))}
              className="select-status"
              placeholder="Selecione"
            >
              <option value="1">Em aberto</option>
              <option value="2">Em andamento</option>
              <option value="3">Matriculada</option>
              <option value="4">Cancelada</option>
            </select>
          </div>
          <div className="input-block">
            <label>Observações do Comercial</label>
            <textarea defaultValue={indication.commercial_note} onChange={(event) => setComercialNote(event.currentTarget.value)}></textarea>
          </div>
          <button className="confirm-button" type="submit">
            Atualizar
          </button>
        </form>
      </fieldset>
    </div>
  );

  return (
    <div className="indication-box">
      <div className="indication-user-information">
        <h4>{Indication.name}</h4>
        <span>{Indication.phone}</span>
      </div>
      <div className="indication-educational-information">
        <h4 className="indication-course">{Indication.course}</h4>
        <span className="indication-instituition">{Indication.instituition}</span>
      </div>
      <div className="indication-buttons">
        <button className="info-button" onClick={() => setOpen(true)}>
          <FiPlus color="#FFF" size={32} />
        </button>
      </div>
      
      <Modal
        open={open}
        onClose={() => setOpen(false)}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center'
        }}
      >
        {modalBody}
      </Modal>
    </div>
  );
}

export default memo(IndicationBox);