import React, { useEffect, useState } from 'react'
import { ColumnChart } from '@opd/g2plot-react'
import api from '../services/api';

export default () => {
  const [data, setData] = useState([]);

  useEffect(() => {
    getGraphicData();
  }, []);

  function getGraphicData() {
    api.get('/graphics/columns')
    .then(response => {
      setData(response.data);
    }).catch(err => {
      console.log(err);
    })
  }

  const config = {
    title: {
      visible: true,
    },
    forceFit: true,
    data: data,
    width: 300,
    height: 200,
    xField: 'instituition',
    yField: 'indications',
    meta: {
      instituition: {
        alias: '类别',
      },
      indications: {
        alias: 'Total',
      },
    },
  }

  return ( 
    <section>
      <ColumnChart {...config} />
    </section>
  );
}