import React, { FormEvent, useEffect, useState, useContext, memo } from 'react';
import IndicationContext from './contexts/IndicationContext';
import { FiPlus } from 'react-icons/fi';
import { Modal } from '@material-ui/core';
import '../styles/components/indication_box.css';
import api from '../services/api';
//import generatePdf from "../components/PDFGenerator"
import jsPDF from 'jspdf';

interface Indication {
  application_user_id: number;
  name: string;
  phone: string;
  course: string;
  instituition: string;
  cpf: string;
  application_user_cpf: String;
  application_user_name: string;
  application_user_lastname: string;
  application_user_phone: string;
  application_user_course_name: String;
  application_user_ra: string;
  commercial_note?: string;
  status: number;
  created_at_formated: string;
}

function generatePdf(name: String, lastName: String, cpf: String, phone: String, indicationName: String, createDate: String, course: String, protocol: Number, indicationCourse: String) {
  const doc = new jsPDF({
    orientation: "p",
    unit: "cm",
    format: "a4",
  });

  doc.setFont('helvetica');
  doc.setFontSize(13);
  doc.text(`TERMO DE ADESÃO AO BENEFÍCIO DO PROGRAMA TOP 10 `, 4, 3, { align: 'justify' });
  doc.text(`Eu ${name} ${lastName},`, 1.5, 4, { align: 'justify' });
  doc.text(`com CPF n°________________ inscrito no RG n° ___________________,`, 1.5, 5, { align: "justify" });
  doc.text(`residente no endereço ____________________________________________________,`, 1.5, 6, { align: "justify" });
  doc.text(`n°__________, CEP ___________________, na cidade de ____________________, `, 1.5, 7, { align: "justify" });
  doc.text(`portador do telefone de número ${phone}, acadêmico regularmente`, 1.5, 8, { align: "justify" });
  doc.text(`matriculado no curso de ${course}, cursando o _____ semestre, na unidade`, 1.5, 9, { align: "justify" });
  doc.text(`_____________________________. Indiquei o (a) cliente de nome`, 1.5, 10, { align: "justify" });
  doc.text(`${indicationName}, no dia`, 1.5, 11, { align: "justify" });
  doc.text(`${createDate}, a estudar na instituição, o (a) mesmo (a) finalizou seu processo de`, 1.5, 12, { align: "justify" })
  doc.text(`matrícula no dia ___/___/______, para o curso de ${indicationCourse}.`, 1.5, 13, { align: "justify" });
  doc.text(`Obedecendo os critérios descritos no regulamento do programa TOP 10, declaro que`, 1.5, 14, { align: 'justify' });
  doc.text(`escolho a opção do benefício:`, 1.5, 15, { align: "justify" });


  doc.text(`( ) PIX DE R$ ________________.`, 1.5, 17, { align: 'justify' });
  doc.setFontSize(11);
  doc.text(`Para o recebimento do benefício em PIX, a chave PIX precisa estar no nome do próprio favorecido;`, 1.5, 18, { align: 'justify' });
  doc.setFontSize(13);
  doc.text(`CHAVE PIX: _____________________________________.`, 1.5, 19, { align: 'justify' });

  doc.text(`( ) 10% de desconto na rematrícula do próximo ano na anualidade;`, 1.5, 20, { align: 'justify' });


  doc.text(`Cabe destacar, que, seguindo os critérios do regulamento do Programa TOP 10, para a`, 1.5, 21, { align: "justify" });
  doc.text(`concessão do benefício, mesmo após o protocolo deste documento, todas as indicações`, 1.5, 22, { align: "justify" });
  doc.text(`serão conferidas.`, 1.5, 23, { align: "justify" });
  doc.text(`______________, _____ de __________ de 20_____.`, 5, 24, { align: "justify" });
  doc.text(`Assinatura______________________________________________`, 1.5, 26, { align: "left" });
  doc.text(`Gestor Comercial_________________________________`, 2, 27, { align: "left" });
  doc.text(`Unidade_________________________________`, 2, 28, { align: "left" });
  doc.text(`${protocol == null ? "" : protocol}`, 1, 29, { align: "left" });

  doc.save();
}

function Top10IndicationBox({ indication }: any) {
  const { getIndications } = useContext(IndicationContext);

  const token = sessionStorage.getItem('@T_A');
  const Indication: Indication = indication;
  const [status, setStatus] = useState('');

  const [statusInput, setIndicationStatusInput] = useState(0);
  const [comercialNote, setComercialNote] = useState('');

  const [open, setOpen] = useState(false);

  useEffect(() => {
    console.log(indication);
    switch (Indication.status) {
      case 1:
        setStatus('Em aberto');
        break;
      case 2:
        setStatus('Indicação Válida');
        break;
      case 3:
        setStatus('Inapta');
        break;
      default:
        setStatus('Indefinido');
    }

    setIndicationStatusInput(Indication.status);
  }, [Indication.status, indication]);

  function updateIndication(event: FormEvent) {
    event.preventDefault();

    const data = {
      status: statusInput,
      note: comercialNote
    };

    api
      .put(`/top10/indications/${indication.id}/treasury`, data, {
        headers: {
          Authorization: 'Bearer ' + token
        }
      })
      .then(response => {
        alert(response.data.message);

        if (response.status === 200) {
          getIndications();
          setOpen(false);
        }
      })
      .catch(err => {
        console.log(err);
      });
  }

  const modalBody = (
    <div className="modal-body">
      <h3>{status}</h3>
      <fieldset>
        <legend>Dados do Indicador</legend>
        <p>
          Nome: {Indication.application_user_name}{' '}
          {Indication.application_user_lastname}
        </p>
        <p>RA: {Indication.application_user_ra}</p>
      </fieldset>
      <fieldset>
        <legend>Indicação</legend>
        <p>Indicado: {Indication.name}</p>
        <p>CPF: {Indication.cpf}</p>
        <p>Telefone: {Indication.phone}</p>
        <p>Curso: {Indication.course}</p>
        <p>Unidade: {Indication.instituition}</p>
        <p>Data da indicação: {Indication.created_at_formated}</p>
        {indication.status === 2 ? <button onClick={() => generatePdf(
          indication.application_user_name,
          indication.application_user_lastname,
          indication.application_user_cpf,
          indication.application_user_phone,
          indication.name,
          indication.created_at_formated,
          indication.application_user_course_name,
          indication.protocol,
          indication.course)}>imprimir</button> : null}

      </fieldset>
      <fieldset>
        <legend>Atualização</legend>
        <form className="update-indication-form" onSubmit={updateIndication}>
          <div className="input-block">
            <label>Situação da indicação</label>
            <select
              defaultValue={statusInput}
              onChange={event =>
                setIndicationStatusInput(parseInt(event.currentTarget.value))
              }
              className="select-status"
              placeholder="Selecione"
            >
              <option value="1">Em aberto</option>
              <option value="2">Válida (Tesouraria)</option>
              <option value="3">Inapta</option>
            </select>
          </div>
          <div className="input-block">
            <label>Observações da Tesouraria</label>
            <textarea
              defaultValue={indication.commercial_note}
              onChange={event => setComercialNote(event.currentTarget.value)}
            ></textarea>
          </div>
          <button className="confirm-button" type="submit">
            Atualizar
          </button>
        </form>
      </fieldset>
    </div>
  );






  return (
    <div className="indication-box">
      <div className="indication-user-information">
        <h4>{Indication.name}</h4>
        <span>{Indication.phone}</span>
      </div>
      <div className="indication-educational-information">
        <h4 className="indication-course">{Indication.course}</h4>
        <span className="indication-instituition">
          {Indication.instituition}
        </span>
      </div>
      <div className="indication-buttons">
        <button className="info-button" onClick={() => setOpen(true)}>
          <FiPlus color="#FFF" size={32} />
        </button>
      </div>

      <Modal
        open={open}
        onClose={() => setOpen(false)}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center'
        }}
      >
        {modalBody}
      </Modal>
    </div>
  );
}

export default memo(Top10IndicationBox);
