import React, { FormEvent, useContext, useState } from 'react';
import { FiTrash, FiEdit } from "react-icons/fi";
import { Modal } from '@material-ui/core';
import '../styles/components/user_box.css';
import api from '../services/api';
import InstuitionContext from '../components/contexts/InstituitionContext';

export default function Top10InstituitionBox({ instituition }: any) {
  const { getInstituitions } = useContext(InstuitionContext);

  const token = sessionStorage.getItem("@T_A");

  const [name, setName] = useState(instituition.name);

  const [editOpen, setEditOpen] = useState(false);
  const [deleteOpen, setDeleteOpen] = useState(false);

  function updateInstituition(event: FormEvent) {
    event.preventDefault();
    
    const data = JSON.stringify({
      name: name
    });

    api.put('/top10/instituitions/' + instituition.id, data, {
      headers: {
        "Content-Type": "application/json",
        "Authorization": "Bearer " + token
      }
    }).then(response => {
      if (response.status === 200) {
        getInstituitions();
        setEditOpen(false);
      }
    }).catch(err => {
      console.log(err);
    });
  }

  function deleteInstituition() {
    api.delete('/top10/instituitions/' + instituition.id, {
      headers: {
        "Authorization": "Bearer " + token,
        "Content-Type": "application/json"
      }
    }).then(response => {
      alert(response.data.message);

      if (response.status === 200) {
        getInstituitions();
        setDeleteOpen(false);
      }
    }).catch(err => {
      console.log(err);
    })
  }
  
  const modalBody = (
    <div className="modal-body">
      <fieldset>
        <legend>Editar instituição</legend>
        <form className="update-user-form" onSubmit={updateInstituition}>
          <div className="input-block">
            <label>Nome Completo</label>
            <input defaultValue={name} onChange={(event) => setName(event.currentTarget.value)}></input>
          </div>
          <button 
            className="confirm-button" 
            type="submit"
            disabled={
              !name
            }
          >
            Atualizar
          </button>
        </form>
      </fieldset>
    </div>
  );
  
  const modelDeleteBody = (
    <div className="modal-body">
      <span>Deseja realmente remover essa instituição?</span>
      <div className="buttons-delete-modal">
        <button
          onClick={() => deleteInstituition()} 
          className="delete-button" 
          type="submit"
        >
          Confirmar
        </button>
        <button
          onClick={() => setDeleteOpen(false)}
          className="cancel-button"
          type="submit"
        >
          Cancelar
        </button>
      </div>
    </div>
  );
  
  return (
    <div className="user-box-box">
      <div className="user-box-user-information">
        <h4>{instituition.name}</h4>
      </div>
      <div className="user-box-buttons">
        <button className="edit-button" onClick={() => setEditOpen(true)}>
          <FiEdit color="#FFF" size={24} />
        </button>
        <button className="trash-button" onClick={() => setDeleteOpen(true)}>
          <FiTrash color="#FFF" size={24} />
        </button>
      </div>
      <Modal
        open={editOpen}
        onClose={() => setEditOpen(false)}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center'
        }}
      >
        {modalBody}
      </Modal>
      <Modal
        open={deleteOpen}
        onClose={() => setDeleteOpen(false)}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center'
        }}
      >
        {modelDeleteBody}
      </Modal>
    </div>
  );
}