import React, { useCallback, useEffect } from 'react';

import { FaUniversity, FaUser, FaList, FaDoorOpen, FaHome, FaHandPointRight, FaBriefcase  } from 'react-icons/fa';
import { useHistory } from 'react-router-dom';
import logo from '../images/logo.png';
import top10logo from '../images/top_10.png';

import '../styles/components/sidebar.css';

interface sidebarProps {
  top10?: boolean
}

export default function Sidebar({ top10 }: sidebarProps) {
  const history = useHistory();

  const token = sessionStorage.getItem("@T_A");
  const user_type = sessionStorage.getItem("@TYPE_U");

  const verifyToken = useCallback(() => {
    if (!token) {
      alert("Sessão expirada, faça o login novamente!");

      history.push("/login");
    }
  }, [history, token]);

  useEffect(() => {
    verifyToken();
  }, [verifyToken]);

  function logout() {
    sessionStorage.removeItem("@T_A");
    sessionStorage.removeItem("@TYPE_U");

    history.push("/");
  }

  return (
    <aside className="sidebar">
      <header>
        {
          top10 ? (
            <>
              <img src={top10logo} alt="Happy Marker" />

              <ul className="list-sidebar">
                <li onClick={() => history.push('/top10/home')}>
                  <FaHome size={34} />
                  <span>
                    Home
                  </span>
                </li>
                <li onClick={() => history.push('/top10/reports')}>
                  <FaHandPointRight size={34} />
                  <span>
                    Indicações
                  </span>
                </li>
                <li onClick={() => history.push('/top10/instituitions')}>
                  <FaUniversity size={34} />
                  <span>
                    Instituições
                  </span>
                </li>
                <li onClick={() => history.push('/top10/courses')}>
                  <FaBriefcase size={34} />
                  <span>
                    Cursos
                  </span>
                </li>
              </ul>
            </>
          ) : (
            <>
              <img src={logo} alt="Happy Marker" />

              <ul className="list-sidebar">
                <li onClick={() => history.push('/home')}>
                  <FaHome size={34} />
                  <span>
                    Home
                  </span>
                </li>
                <li onClick={() => history.push('/indications')}>
                  <FaHandPointRight size={34} />
                  <span>
                    Indicações
                  </span>
                </li>
                <li onClick={() => history.push('/reports')}>
                  <FaList size={34} />
                  <span>
                    Relatórios
                  </span>
                </li>
                <li onClick={() => history.push('/courses')}>
                  <FaBriefcase size={34} />
                  <span>
                    Cursos
                  </span>
                </li>
                <li onClick={() => history.push('/sectors')}>
                  <FaBriefcase size={34} />
                  <span>
                    Setores
                  </span>
                </li>
                <li onClick={() => history.push('/instituitions')}>
                  <FaUniversity size={34} />
                  <span>
                    Instituições
                  </span>
                </li>
                <li onClick={() => history.push('/top10/home')}>
                  <FaUniversity size={34} />
                  <span>
                    TOP 10
                  </span>
                </li>
                {
                  user_type === '2' ? (
                    <li onClick={() => history.push('/users')}>
                      <FaUser size={34} />
                      <span>
                        Usuários
                      </span>
                    </li>
                  ) : (
                    null
                  )
                }
              </ul>
            </>
          )
        }

        
      </header>

      <footer>
        <li onClick={() => logout()}>
          <FaDoorOpen size={34} />
          <span>
            Sair
          </span>
        </li>
      </footer>
    </aside>
  );
}