import React, { useCallback, useEffect } from 'react';

import { FiArrowLeft } from "react-icons/fi";
import { useHistory } from 'react-router-dom';
import icon from '../images/icon.png';

import '../styles/components/sidebar_mini.css';

interface sidebarProps {
  top10?: boolean
}

export default function SidebarMini({ top10 }: sidebarProps) {
  const history = useHistory();

  const token = sessionStorage.getItem("@T_A");

  const verifyToken = useCallback(() => {
    if (!token) {
      alert("Sessão expirada, faça o login novamente!");

      history.push("/");
    }
  }, [history, token]);

  useEffect(() => {
    verifyToken();
  }, [verifyToken]);

  return (
    <aside className="app-sidebar">
      <img src={icon} alt="Happy" />

      <footer>
        {
          top10 ? (
            <button type="button" onClick={() => history.push('/top10/home')}>
              <FiArrowLeft size={24} color="#FFF" />
            </button>
          ) : (
            <button type="button" onClick={() => history.push('/home')}>
              <FiArrowLeft size={24} color="#FFF" />
            </button>
          )
        }
      </footer>
    </aside>
  );
}