import React, { FormEvent, useState } from 'react';
import { FiTrash, FiEdit } from "react-icons/fi";
import { Modal } from '@material-ui/core';
import '../styles/components/user_box.css';
import api from '../services/api';

interface User {
  id: number;
  name: string;
  email: string;
  type: string;
}

export default function IndicationBox({ user, getUsers }: any) {
  const token = "";
  const User: User = user;
  const [name, setName] = useState(user.name);
  const [email, setEmail] = useState(user.email);
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [userType, setUserType] = useState(user.type);

  const [editOpen, setEditOpen] = useState(false);
  const [deleteOpen, setDeleteOpen] = useState(false);

  function updateUser(event: FormEvent) {
    event.preventDefault();
    
    const data = JSON.stringify({
      name: name,
      email: email,
      password: password,
      type: userType
    });

    api.put('/users/dashboard/' + User.id, data, {
      headers: {
        "Content-Type": "application/json",
        "Authorization": "Bearer " + token
      }
    }).then(response => {
      alert(response.data.message);

      if (response.status === 200) {
        getUsers();
        setEditOpen(false);
      }
    }).catch(err => {
      console.log(err);
    });
  }

  function deleteUser() {
    api.delete('/users/dashboard/' + User.id, {
      headers: {
        "Authorization": "Bearer " + token,
        "Content-Type": "application/json"
      }
    }).then(response => {
      alert(response.data.message);

      if (response.status === 200) {
        getUsers();
        setDeleteOpen(false);
      }
    }).catch(err => {
      console.log(err);
    })
  }
  
  const modalBody = (
    <div className="modal-body">
      <fieldset>
        <legend>Editar usuário</legend>
        <form className="update-user-form" onSubmit={updateUser}>
          <div className="input-block">
            <label>Nome Completo</label>
            <input defaultValue={name} onChange={(event) => setName(event.currentTarget.value)}></input>
          </div>
          <div className="input-block">
            <label>E-mail</label>
            <input type="email" defaultValue={email} onChange={(event) => setEmail(event.currentTarget.value)}></input>
          </div>
          <div className="input-block">
            <label>Senha</label>
            <input type="password" onChange={(event) => setPassword(event.currentTarget.value)}></input>
          </div>
          <div className="input-block">
            <label>Confirmar Senha</label>
            <input type="password" onChange={(event) => setConfirmPassword(event.currentTarget.value)}></input>
          </div>
          <div className="input-block">
            <label>Tipo do usuário</label>
            <select defaultValue={userType} onChange={(event) => setUserType(parseInt(event.currentTarget.value))} className="select-status" placeholder="Selecione">
              <option value="1">Setor Comercial</option>
              <option value="2">Administrador</option>
            </select>
          </div>
          <button 
            className="confirm-button" 
            type="submit"
            disabled={
              !name || !email || password !== confirmPassword
            }
          >
            Atualizar
          </button>
        </form>
      </fieldset>
    </div>
  );
  
  const modelDeleteBody = (
    <div className="modal-body">
      <span>Deseja realmente remover esse usuário?</span>
      <div className="buttons-delete-modal">
        <button onClick={() => deleteUser()} className="delete-button" type="submit">
          Confirmar
        </button>
        <button onClick={() => setDeleteOpen(false)} className="cancel-button" type="submit">
          Cancelar
        </button>
      </div>
    </div>
  );
  
  return (
    <div className="user-box-box">
      <div className="user-box-user-information">
        <h4>{User.name}</h4>
        <span>{User.email}</span>
      </div>
      <div className="user-box-buttons">
        <button className="edit-button" onClick={() => setEditOpen(true)}>
          <FiEdit color="#FFF" size={24} />
        </button>
        <button className="trash-button" onClick={() => setDeleteOpen(true)}>
          <FiTrash color="#FFF" size={24} />
        </button>
      </div>
      <Modal
        open={editOpen}
        onClose={() => setEditOpen(false)}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center'
        }}
      >
        {modalBody}
      </Modal>
      <Modal
        open={deleteOpen}
        onClose={() => setDeleteOpen(false)}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center'
        }}
      >
        {modelDeleteBody}
      </Modal>
    </div>
  );
}