import React, { FormEvent, useState } from 'react';
import { useHistory } from 'react-router-dom';

import '../styles/pages/landing.css';
import logoImg from '../images/logo.png';
import api from '../services/api';

export default function Landing() {
  const history = useHistory();

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  function login(event: FormEvent) {
    event.preventDefault();

    const data = JSON.stringify({
      email: email,
      password: password
    });

    api.post('/users/dashboard/login', data, {
      headers: {
        "Content-Type": "application/json"
      }
    }).then(response => {
      if (response.data.token) {
        sessionStorage.setItem('@T_A', response.data.token);
        sessionStorage.setItem('@TYPE_U', response.data.type);

        if (response.data.type == '3') {
          history.push('/top10/home');
        } else {
          history.push('/home');
        }
      } else {
        alert(response.data.message);
      }
    }).catch(err => {
      alert("Falha na autenticação!");
    })
  }

  return (
    <div id="page-landing">
      <div className="content-wrapper">
        <main>
          <h1>Sistema de Indicações</h1>
        </main>

        <img src={logoImg} alt="Logo Happy"/>

        <div className="location">
          <form onSubmit={login} className="indication-form">
            <fieldset>
              <legend>Login</legend>
              <div className="input-block">
                <label htmlFor="email">E-mail</label>
                <input 
                  id="email" 
                  type="email"
                  value={email} 
                  onChange={event => setEmail(event.target.value)}
                />
              </div>
              <div className="input-block">
                <label htmlFor="password">Senha</label>
                <input 
                  id="password"
                  type="password"
                  value={password} 
                  onChange={event => setPassword(event.target.value)}
                />
              </div>
            </fieldset>
            <button className="confirm-button" type="submit">
              Entrar
            </button>
          </form>
        </div>
      </div>
    </div>
  );
}