import React, { FormEvent, useState, useEffect, useCallback } from 'react';
import IndicationBox from '../components/IndicationBox';
import SidebarMini from '../components/SidebarMini';
import { FaCog } from 'react-icons/fa';
import Pagination from '../components/Pagination';
import api from '../services/api';
import '../styles/pages/indications.css';
import IndicationContext from '../components/contexts/IndicationContext';

const emptyGif = require('../images/empty_gif.gif');

interface Indication {
  id: number;
  application_user_id: number;
  name: string;
  phone: string;
  course: string;
  course_id: number;
  instituition: string;
  instituition_id: number;
  application_user_name: string;
  application_user_phone: string;
  commercial_note?: string;
  status: number;
}

interface Course {
  id: number;
  name: string;
  alias?: string;
}

interface Instituition {
  id: number;
  name: string;
}

export default function Indications() {
  const token = sessionStorage.getItem('@T_A');

  const [progressFilter, setProgressFilter] = useState(1);
  const [instituitionFilter, setInstituitionFilter] = useState(0);
  const [courseFilter, setCourseFilter] = useState(0);

  const [indications, setIndications] = useState<Indication[]>([]);
  const [courses, setCourses] = useState<Course[]>([]);
  const [instituitions, setInstituitions] = useState<Instituition[]>([]);
  const [filteredIndications, setFilteredIndications] = useState<Indication[]>(
    []
  );

  const [loading, setLoading] = useState(false);

  const [currentPage, setCurrentPage] = useState(1);
  const [postsPerPage] = useState(15);
  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;
  const currentIndications = filteredIndications.slice(
    indexOfFirstPost,
    indexOfLastPost
  );

  const getIndications = useCallback(() => {
    setLoading(true);

    api
      .get('/indications', {
        headers: {
          Authorization: 'Bearer ' + token,
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        setIndications(response.data);
        setFilteredIndications(response.data);
        setLoading(false);
      })
      .catch(err => {
        console.log('Error: ', err);
      });
  }, [token]);

  const getCourses = useCallback(() => {
    setLoading(true);

    api
      .get('/courses', {
        headers: {
          Authorization: 'Bearer ' + token,
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        setCourses(response.data);
        setLoading(false);
      })
      .catch(err => {
        console.log('Error: ', err);
      });
  }, [token]);

  const getInstituitions = useCallback(() => {
    setLoading(true);

    api
      .get('/instituitions', {
        headers: {
          Authorization: 'Bearer ' + token,
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        setInstituitions(response.data);
        setLoading(false);
      })
      .catch(err => {
        console.log('Error: ', err);
      });
  }, [token]);

  useEffect(() => {
    getIndications();
    getInstituitions();
    getCourses();
  }, [getCourses, getIndications, getInstituitions]);

  function filterIndications(event: FormEvent) {
    event.preventDefault();

    setLoading(true);

    api
      .get(
        `/indications/filter/?course=${courseFilter}&instituition=${instituitionFilter}&status=${progressFilter}`,
        {
          headers: {
            Authorization: 'Bearer ' + token,
            'Content-Type': 'application/json'
          }
        }
      )
      .then(response => {
        setIndications(response.data);
        setFilteredIndications(response.data);
        setLoading(false);
      })
      .catch(err => {
        console.log(err);
      });
  }

  function paginate(pageNumber: number) {
    setCurrentPage(pageNumber);
  }

  function filterByIndicationName(name: string) {
    const value = name.toLowerCase();

    const filteredNamesArr = indications.filter(indication =>
      indication.application_user_name.toLowerCase().includes(value)
    );
    

    setFilteredIndications(filteredNamesArr);
    setCurrentPage(1);
  }

  return (
    <div id="page-indications">
      <SidebarMini />

      <main>
        <div className="indications-boxes-content">
          <form className="filter-form" onSubmit={filterIndications}>
            <div className="input-block">
              <label>Progresso</label>
              <select
                onChange={event =>
                  setProgressFilter(parseInt(event.currentTarget.value))
                }
                className="select-status"
                placeholder="Progresso"
              >
                <option value="1" defaultValue="1">
                  Em aberto
                </option>
                <option value="2">Em andamento</option>
                <option value="3">Matriculada</option>
                <option value="4">Cancelada</option>
              </select>
            </div>
            <div className="input-block">
              <label>Unidade</label>
              <select
                onChange={event =>
                  setInstituitionFilter(parseInt(event.currentTarget.value))
                }
                className="select-status"
                placeholder="Progresso"
              >
                <option value="0" defaultValue="0">
                  Não selecionado
                </option>
                {instituitions.map(instituition => (
                  <option key={instituition.id} value={instituition.id}>
                    {instituition.name}
                  </option>
                ))}
              </select>
            </div>
            <div className="input-block">
              <label>Curso</label>
              <select
                onChange={event =>
                  setCourseFilter(parseInt(event.currentTarget.value))
                }
                className="select-status"
                placeholder="Curso"
              >
                <option value="0" defaultValue="0">
                  Não selecionado
                </option>
                {courses.map(course => (
                  <option key={course.id} value={course.id}>
                    {course.name}
                  </option>
                ))}
              </select>
            </div>
            <button className="confirm-button-filter" type="submit">
              Filtrar
            </button>
            <div className="input-block">
              <label>Pesquisar por nome do Indicador</label>
              <input
                type="text"
                onChange={event => filterByIndicationName(event.target.value)}
              />
            </div>
          </form>
          {loading ? (
            <div className="rotate-box">
              <FaCog size={30} color="#3CDC8C" className="rotating" />
            </div>
          ) : currentIndications.length > 0 ? (
            <IndicationContext.Provider value={{ getIndications }}>
              {currentIndications.map(indication => (
                <IndicationBox key={indication.id} indication={indication} />
              ))}
            </IndicationContext.Provider>
          ) : (
            <div className="no-data-founded">
              <img
                src={emptyGif}
                width="200"
                alt="Nenhuma indicação encontrada"
              />
              <span>Parece que não existem indicações a serem filtradas.</span>
            </div>
          )}
          {loading ? null : (
            <div className="pagination">
              <Pagination
                postsPerPage={postsPerPage}
                totalPosts={filteredIndications.length}
                paginate={paginate}
              />
            </div>
          )}
        </div>
      </main>
    </div>
  );
}
