import React, { useEffect, useState } from 'react';
import '../styles/pages/verify.css';
import api from "../services/api";
import { FaCheck, FaExclamationTriangle, FaSpinner } from 'react-icons/fa';

export default function Verify() {
  const [loading, setLoading] = useState(true);
  const [message, setMessage] = useState('');
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState(false);

  useEffect(() => {
    verifyEmail();
  }, [])

  function verifyEmail() {
    let hashed = window.location.href.toString().split("/verify/")[1];

    if (!hashed) {
      alert("Opa")
    }

    const data = JSON.stringify({
      hash: hashed
    });

    api.put("/users/application/verify-email", data, {
      headers: {
        "Content-Type": "application/json"
      }
    }).then(response => {
      setTimeout(() => {
        setLoading(false);
        if (response.status === 200) {
          setSuccess(true);
        } else {
          setError(true);
        }
        setMessage(response.data.message)
      }, 4000)
    })
    .catch(err => {
      setLoading(false);
      setError(true);
      setMessage("Algo de errado aconteceu. Tente novamente mais tarde.")
    })
  }

  return (
    <div className="main">
      <div className="box-center" id="box">
          { loading
          ? 
          <div className="rotate-box">
            <FaSpinner size={50} color="#3CDC8C" className="rotating" />
          </div>
          : null
          }
          { success ? 
          <FaCheck style={{fontSize: 130, color: "#37B307"}}/>
          : null
          }
          
          { error ? 
          <FaExclamationTriangle style={{fontSize: 130, color: "#FA685E"}}/>
          : null
          }
          { message
          ? <div style={{width: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
              <span style={{fontSize: 32, fontWeight: 'bold', lineHeight: 1.5, textAlign: 'center', color: "#37B307"}}>
                {message}
              </span>
            </div>
          : null
          }
      </div>
    </div>
  );
}