import React, { FormEvent, useState, useEffect, useCallback } from "react";
import UserBox from '../components/UserBox';
import SidebarMini from '../components/SidebarMini';
import { FaCog } from "react-icons/fa";
import api from "../services/api";
import '../styles/pages/users.css';
import { FiPlus, FiAlertTriangle } from "react-icons/fi";
import { Modal } from "@material-ui/core";

interface User {
  name: string;
  email: string;
  type: number;
}

export default function Users() {
  const token = sessionStorage.getItem("@T_A");

  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(false);

  const [users, setUsers] = useState<User[]>([]);

  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [userType, setUserType] = useState(1);
  
  const getUsers = useCallback(() => {
    setLoading(true);

    api.get('/users/dashboard', {
      headers: {
        "Authorization": "Bearer " + token,
        "Content-Type": "application/json"
      }
    }).then(response => {
      setUsers(response.data);
      setLoading(false);
    }).catch(err => {
      console.log("Error: ", err);
    });
  }, [token]);

  useEffect(() => {
    getUsers();
  }, [getUsers]);

  function createUser(event: FormEvent) {
    event.preventDefault();
    
    const data = JSON.stringify({
      name: name,
      email: email,
      password: password,
      type: userType
    });

    api.post('/users/dashboard', data, {
      headers: {
        "Content-Type": "application/json",
        "Authorization": "Bearer " + token
      }
    }).then(response => {
      alert(response.data.message);

      if (response.status === 200) {
        getUsers();
        setOpen(false);
      }
    }).catch(err => {
      console.log(err);
    });
  }

  const modalBody = (
    <div className="modal-body">
      <fieldset>
        <legend>Cadastrar usuário</legend>
        <form className="update-user-form" onSubmit={createUser}>
          <div className="input-block">
            <label>Nome Completo</label>
            <input defaultValue={name} onChange={(event) => setName(event.currentTarget.value)}></input>
          </div>
          <div className="input-block">
            <label>E-mail</label>
            <input type="email" defaultValue={email} onChange={(event) => setEmail(event.currentTarget.value)}></input>
          </div>
          <div className="input-block">
            <label>Senha</label>
            <input type="password" onChange={(event) => setPassword(event.currentTarget.value)}></input>
          </div>
          <div className="input-block">
            <label>Confirmar Senha</label>
            <input type="password" onChange={(event) => setConfirmPassword(event.currentTarget.value)}></input>
          </div>
          { password !== confirmPassword ? 
          (
            <span className="not-same-password">
              <FiAlertTriangle size={18} color="red" />
              As senhas não batem.
            </span> 
          )
          : (null) }
          <div className="input-block">
            <label>Tipo do usuário</label>
            <select defaultValue={userType} onChange={(event) => setUserType(parseInt(event.currentTarget.value))} className="select-status" placeholder="Selecione">
              <option value="1">Setor Comercial</option>
              <option value="2">Administrador</option>
              <option value="3">Setor Financeiro</option>
            </select>
          </div>
          <button
            className="confirm-button"
            type="submit"
            disabled={
              !name || !email || !password || !confirmPassword || password !== confirmPassword
            }>
            Cadastrar
          </button>
        </form>
      </fieldset>
    </div>
  );

  return (
    <div id="page-users">
      <SidebarMini />

      <main>
        <div className="indications-boxes-content">
        <button className="add-button" type="button" onClick={() => setOpen(true)}>
          Adicionar
          <FiPlus size={32} color="white" />
        </button>
          {
            loading ? 
            (
              <div className="rotate-box">
                <FaCog size={30} color="#3CDC8C" className="rotating" />
              </div>
            ) : (
              users.map(indication => (
                <UserBox user={indication} getUsers={getUsers} />
              ))
            )
          }
        </div>
        
        <Modal
          open={open}
          onClose={() => setOpen(false)}
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description"
          style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center'
          }}
        >
          {modalBody}
        </Modal>
      </main>
    </div>
  );
}
