import React, { FormEvent, useState, useEffect, useCallback } from "react";
import SidebarMini from '../../components/SidebarMini';
import { FaCog } from "react-icons/fa";
import api from "../../services/api";
import '../../styles/pages/users.css';
import { FiPlus } from "react-icons/fi";
import { Modal } from "@material-ui/core";
import InstituitionContext from '../../components/contexts/InstituitionContext';
import Top10InstituitionBox from "../../components/Top10InstituitionBox";

interface Instituition {
  id: number;
  name: string;
}

export default function Top10Instituitions() {
  const token = sessionStorage.getItem("@T_A");

  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(false);

  const [instituitions, setInstituitions] = useState<Instituition[]>([]);

  const [name, setName] = useState('');
  
  const getInstituitions = useCallback(() => {
    setLoading(true);

    api.get('/top10/instituitions', {
      headers: {
        "Authorization": "Bearer " + token,
        "Content-Type": "application/json"
      }
    }).then(response => {
      setInstituitions(response.data);
      setLoading(false);
    }).catch(err => {
      console.log("Error: ", err);
    });
  }, [token]);

  useEffect(() => {
    getInstituitions();
  }, [getInstituitions]);

  function createInstituition(event: FormEvent) {
    event.preventDefault();
    
    const data = JSON.stringify({
      name: name
    });

    api.post('/top10/instituitions', data, {
      headers: {
        "Content-Type": "application/json",
        "Authorization": "Bearer " + token
      }
    }).then(response => {
      alert(response.data.message);

      if (response.status === 200) {
        getInstituitions();
        setOpen(false);
      }
    }).catch(err => {
      console.log(err);
    });
  }

  const modalBody = (
    <div className="modal-body">
      <fieldset>
        <legend>Cadastrar instituição</legend>
        <form className="update-user-form" onSubmit={createInstituition}>
          <div className="input-block">
            <label>Nome Completo</label>
            <input defaultValue={name} onChange={(event) => setName(event.currentTarget.value)}></input>
          </div>
          <button
            className="confirm-button"
            type="submit"
            disabled={
              !name
            }>
            Cadastrar
          </button>
        </form>
      </fieldset>
    </div>
  );

  return (
    <div id="page-users">
      <SidebarMini top10={true} />

      <main>
        <div className="indications-boxes-content">
        <button className="add-button" type="button" onClick={() => setOpen(true)}>
          Adicionar
          <FiPlus size={32} color="white" />
        </button>
          {
            loading ? 
            (
              <div className="rotate-box">
                <FaCog size={30} color="#3CDC8C" className="rotating" />
              </div>
            ) : (
              <InstituitionContext.Provider value={{ getInstituitions }}>
                {
                  instituitions.map((instituition, index) => (
                    <Top10InstituitionBox key={index} instituition={instituition} />
                  ))
                }
              </InstituitionContext.Provider>
            )
          }
        </div>
        
        <Modal
          open={open}
          onClose={() => setOpen(false)}
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description"
          style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center'
          }}
        >
          {modalBody}
        </Modal>
      </main>
    </div>
  );
}
