import React, { FormEvent, useContext, useState } from 'react';
import { FiTrash, FiEdit } from "react-icons/fi";
import { Modal } from '@material-ui/core';
import '../styles/components/user_box.css';
import api from '../services/api';
import SectorContext from './contexts/SectorContext';

interface Sector {
  id: number;
  name: string;
}

export default function SectorBox({ sector }: any) {
  const { getSectors } = useContext(SectorContext);

  const token = sessionStorage.getItem("@T_A");

  const Sector: Sector = sector;
  const [name, setName] = useState(Sector.name);

  const [editOpen, setEditOpen] = useState(false);
  const [deleteOpen, setDeleteOpen] = useState(false);

  function updateCourse(event: FormEvent) {
    event.preventDefault();
    
    const data = JSON.stringify({
      name: name
    });

    api.put('/sectors/' + Sector.id, data, {
      headers: {
        "Content-Type": "application/json",
        "Authorization": "Bearer " + token
      }
    }).then(response => {
      alert(response.data.message);

      if (response.status === 200) {
        getSectors();
        setEditOpen(false);
      }
    }).catch(err => {
      console.log(err);
    });
  }

  function deleteCourse() {
    api.delete('/sectors/' + Sector.id, {
      headers: {
        "Authorization": "Bearer " + token,
        "Content-Type": "application/json"
      }
    }).then(response => {
      alert(response.data.message);

      if (response.status === 200) {
        getSectors();
        setDeleteOpen(false);
      }
    }).catch(err => {
      console.log(err);
    })
  }
  
  const modalBody = (
    <div className="modal-body">
      <fieldset>
        <legend>Editar setor</legend>
        <form className="update-user-form" onSubmit={updateCourse}>
          <div className="input-block">
            <label>Nome</label>
            <input defaultValue={name} onChange={(event) => setName(event.currentTarget.value)}></input>
          </div>
          <button 
            className="confirm-button" 
            type="submit"
            disabled={
              !name
            }
          >
            Atualizar
          </button>
        </form>
      </fieldset>
    </div>
  );
  
  const modelDeleteBody = (
    <div className="modal-body">
      <span>Deseja realmente remover esse setor?</span>
      <div className="buttons-delete-modal">
        <button onClick={() => deleteCourse()} className="delete-button" type="submit">
          Confirmar
        </button>
        <button onClick={() => setDeleteOpen(false)} className="cancel-button" type="submit">
          Cancelar
        </button>
      </div>
    </div>
  );
  
  return (
    <div className="user-box-box">
      <div className="user-box-user-information">
        <h4>{Sector.name}</h4>
      </div>
      <div className="user-box-buttons">
        <button className="edit-button" onClick={() => setEditOpen(true)}>
          <FiEdit color="#FFF" size={24} />
        </button>
        <button className="trash-button" onClick={() => setDeleteOpen(true)}>
          <FiTrash color="#FFF" size={24} />
        </button>
      </div>
      <Modal
        open={editOpen}
        onClose={() => setEditOpen(false)}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center'
        }}
      >
        {modalBody}
      </Modal>
      <Modal
        open={deleteOpen}
        onClose={() => setDeleteOpen(false)}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center'
        }}
      >
        {modelDeleteBody}
      </Modal>
    </div>
  );
}