import React, { useCallback, useEffect, useState } from 'react';

import '../styles/pages/home-page.css';
import api from '../services/api';

import Sidebar from '../components/Sidebar';
import PieGraph from '../components/PieGraph';
import ColumnGraph from '../components/ColumnGraph';

interface Totalizer {
  status: string;
  value: number;
}

interface Instituition {
  id: number;
  name: string;
}

export default function Home() {
  const token = sessionStorage.getItem("@T_A");

  const [total, setTotal] = useState(0);
  const [opened, setOpened] = useState(0);
  const [progress, setProgress] = useState(0);
  const [finished, setFinished] = useState(0);
  const [canceled, setCanceled] = useState(0);

  const [indicators, setIndicators] = useState(0);

  const [instituitions, setInstituitions] = useState<Instituition[]>([]);

  const totalizers = useCallback(() => {
    api.get('/indications/totalizers', {
      headers: {
        "Authorization": `Bearer ${token}`
      }
    }).then(response => {
        let total: number = 0;
        const { data } = response;

        data.map((totalizer: Totalizer) => {
          total = total + totalizer.value;

          if (totalizer.status === '1') {
            setOpened(totalizer.value);
          } else if (totalizer.status === '2') {
            setProgress(totalizer.value);
          } else if (totalizer.status === '3') {
            setFinished(totalizer.value);
          } else {
            setCanceled(totalizer.value);
          }

          return null;
        });

        setTotal(total);
      }).catch(err => {
        console.log(err);
      })
  }, [token]);

  function totalizersPerInstituition(event: any) {
    const id_instituition = parseInt(event.currentTarget.value);

    if (!id_instituition) {
      totalizers();
      quantityIndicators();

      return;
    }

    getQuantityIndicatorsPerInstituition(id_instituition);

    api.get(`/indications/totalizers/instituition/${id_instituition}`, {
      headers: {
        "Authorization": `Bearer ${token}`
      }
    })
    .then(response => {
      let total: number = 0;
      const { data } = response;

      data.map((totalizer: Totalizer) => {
        total = total + totalizer.value;

        if (totalizer.status === '1') {
          setOpened(totalizer.value);
        } else if (totalizer.status === '2') {
          setProgress(totalizer.value);
        } else if (totalizer.status === '3') {
          setFinished(totalizer.value);
        } else {
          setCanceled(totalizer.value);
        }

        return null;
      });

      setTotal(total);
    }).catch(err => {
      console.log(err);
    })
  }

  function getQuantityIndicatorsPerInstituition(id_instituition: number) {
    api.get(`/users/application/indicators/instituition/${id_instituition}`, {
      headers: {
        "Authorization": `Bearer ${token}`
      }
    }).then(response => {
      console.log(response.data.length, 'qtd indi')
      setIndicators(response.data.length);
    }).catch(err => {
      console.log(err);
    })
  }

  const quantityIndicators = useCallback(() => {
    api.get('/users/application/indicators', {
      headers: {
        "Authorization": `Bearer ${token}`
      }
    }).then(response => {
      setIndicators(response.data.length);
    }).catch(err => {
      console.log(err);
    })
  }, [token]);
  
  const getInstituitions = useCallback(() => {
    api.get('/instituitions', {
      headers: {
        "Authorization": `Bearer ${token}`
      }
    }).then(response => {
      setInstituitions(response.data);
    }).catch(err => {
      console.log(err);
    })
  }, [token]);

  useEffect(() => {
    totalizers();
    quantityIndicators();
    getInstituitions();
  }, [quantityIndicators, totalizers]);

  return (
    <div id="page-map">
      <Sidebar />

      <main>
        <div className="content">
          <div className="input-block">
            <label>Seleciona a instituição:</label>
            <select
              onChange={(event) => totalizersPerInstituition(event)}
              className="select-status"
              placeholder="Instituição"
            >
              <option value="0" selected>Não selecionado</option>
              {
                instituitions.map(instituition => (
                  <option key={instituition.id} value={instituition.id}>{instituition.name}</option>
                ))
              }
            </select>
          </div>

          <div className="totality-indications">
            <h4>Total</h4>
            <span>{total}</span>
          </div>
          <div className="totality-indications">
            <h4>Em aberto</h4>
            <span>{opened}</span>
          </div>
          <div className="totality-indications">
            <h4 className="progress">Em andamento</h4>
            <span>{progress}</span>
          </div>
          <div className="totality-indications">
            <h4 className="finished">Finalizadas</h4>
            <span>{finished}</span>
          </div>
          <div className="totality-indications">
            <h4 className="canceled">Canceladas</h4>
            <span>{canceled}</span>
          </div>
          <div className="totality-indicators">
            <h4 className="indicators">Qtd. Indicadores</h4>
            <span>{indicators}</span>
          </div>

          <span className="observation">* Os gráficos abaixo se referem ao contexto geral das indicações. O filtro acima não intefere nos gráficos.</span>

          <div className="graphs">
            <div style={{marginRight: 20}}>
              <PieGraph />
            </div>
            <div>
              <ColumnGraph />
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}