import React, { FormEvent, useState, useEffect, useCallback } from "react";
import SidebarMini from '../components/SidebarMini';
import { FaCog } from "react-icons/fa";
import api from "../services/api";
import '../styles/pages/users.css';
import { FiPlus } from "react-icons/fi";
import { Modal } from "@material-ui/core";
import SectorBox from "../components/SectorBox";
import SectorContext from "../components/contexts/SectorContext";

interface Sector {
  id: number;
  name: string;
}

export default function Sectors() {
  const token = sessionStorage.getItem("@T_A");

  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(false);

  const [sectors, setSectors] = useState<Sector[]>([]);

  const [name, setName] = useState('');
  
  const getSectors = useCallback(() => {
    setLoading(true);

    api.get('/sectors', {
      headers: {
        "Authorization": "Bearer " + token,
        "Content-Type": "application/json"
      }
    }).then(response => {
      setSectors(response.data);
      setLoading(false);
    }).catch(err => {
      console.log("Error: ", err);
    });
  }, [token]);

  useEffect(() => {
    getSectors();
  }, [getSectors]);

  function createSector(event: FormEvent) {
    event.preventDefault();
    
    const data = JSON.stringify({
      name: name
    });

    api.post('/sectors', data, {
      headers: {
        "Content-Type": "application/json",
        "Authorization": "Bearer " + token
      }
    }).then(response => {
      alert(response.data.message);

      if (response.status === 200) {
        getSectors();
        setOpen(false);
      }
    }).catch(err => {
      console.log(err);
    });
  }

  const modalBody = (
    <div className="modal-body">
      <fieldset>
        <legend>Cadastrar setor</legend>
        <form className="update-user-form" onSubmit={createSector}>
          <div className="input-block">
            <label>Nome</label>
            <input defaultValue={name} onChange={(event) => setName(event.currentTarget.value)}></input>
          </div>
          <button
            className="confirm-button"
            type="submit"
            disabled={
              !name
            }>
            Cadastrar
          </button>
        </form>
      </fieldset>
    </div>
  );

  return (
    <div id="page-users">
      <SidebarMini />

      <main>
        <div className="indications-boxes-content">
        <button className="add-button" type="button" onClick={() => setOpen(true)}>
          Adicionar
          <FiPlus size={32} color="white" />
        </button>
          {
            loading ? 
            (
              <div className="rotate-box">
                <FaCog size={30} color="#3CDC8C" className="rotating" />
              </div>
            ) : (
              <SectorContext.Provider value={{ getSectors }}>
                {
                  sectors.map(sector => (
                    <SectorBox key={sector.id} sector={sector} />
                  ))
                }
              </SectorContext.Provider>
            )
          }
        </div>
        
        <Modal
          open={open}
          onClose={() => setOpen(false)}
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description"
          style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center'
          }}
        >
          {modalBody}
        </Modal>
      </main>
    </div>
  );
}
