import React from 'react';
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom';

import Landing from './pages/Landing';
import Home from './pages/Home';
import Indications from './pages/Indications';
import Reports from './pages/Reports';
import NotFound from './pages/NotFound';
import Users from './pages/Users';
import Verify from './pages/Verify';
import RecoverPassword from './pages/RecoverPassword';
import Courses from './pages/Courses';
import Instituitions from './pages/Instituitions';

// Imports TOP 10

import T10_Home from './pages/top10/Home';
import T10_Reports from './pages/top10/Reports';
import T10_Courses from './pages/top10/Courses';
import T10_Instituitions from './pages/top10/Instituitions';

import { isAuthenticated } from './services/isAuthenticated';
import Sectors from './pages/Sectors';

const PrivateRoute = ({ component: Component, ...rest }) => (
  <Route {...rest} render={props => (
      isAuthenticated() ? (
        <Component {...props} />
      ) : (
        <Redirect to={{ pathname: '/', state: { from: props.location } }} />
      )
    )
  } /> 
)

export default function Routes() {
  return (
    <BrowserRouter>
      <Switch>
        <Route exact path="/" component={Landing} />
        <PrivateRoute path="/home" component={Home} />
        
        <PrivateRoute path="/indications" component={Indications} />
        <PrivateRoute path="/reports" component={Reports} />
        <PrivateRoute path="/users" component={Users} />
        <PrivateRoute path="/courses" component={Courses} />
        <PrivateRoute path="/sectors" component={Sectors} />
        <PrivateRoute path="/instituitions" component={Instituitions} />
        
        <Route path="/verify/:hash" component={Verify} />
        <Route path="/recover-password/:hash" component={RecoverPassword} />

        {
          /**
           * Rotas TOP 10
           */
        }

        <Route path="/top10/home" component={T10_Home} />
        <Route path="/top10/reports" component={T10_Reports} />
        <Route path="/top10/courses" component={T10_Courses} />
        <Route path="/top10/instituitions" component={T10_Instituitions} />

        <Route path="*" component={NotFound} />
      </Switch>
    </BrowserRouter>
  );
}